<template>
  <div id="app">
    <Exam/>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
import Exam from './components/Exam'
import Category from './components/Category'

export default {
  name: 'App',
  components: {
    Exam,Category
  }
}
</script>

<style>
:root{
  --theme-color:#375e96;
  --footer-color:#333333;
  --background-color: #f0f3f9;
}
@font-face {
  font-family: 'iconfont';  /* project id 544540 */
  src: url('//at.alicdn.com/t/font_544540_92fd4bzap8mbcsor.eot');
  src: url('//at.alicdn.com/t/font_544540_92fd4bzap8mbcsor.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_544540_92fd4bzap8mbcsor.woff') format('woff'),
  url('//at.alicdn.com/t/font_544540_92fd4bzap8mbcsor.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_544540_92fd4bzap8mbcsor.svg#iconfont') format('svg');
}
html,body{
  margin:0;
  padding:0;
  width:100%;
  height: 100%;
  overflow:scroll;  
  overflow-y:hidden;  
  background: var(--background-color);
  font-family: "bichimel";
}
#app {
  margin:0;
  padding:0;
  line-height: 1.5;
  page-break-before: auto;
  page-break-after: always;
  min-height: 100%;
  height: auto !important;
  height:100%;
  min-width: 100%;
  position: relative;
}
.content{
    box-sizing: border-box;
    margin: 0 auto;
    // border: 1px solid green;
    min-height: 100%;
    height: auto !important;
    height:100%;
    padding: 0px 10px 10px 0;
  }
.mongol{
  -moz-writing-mode: vertical-lr;
  -webkit-writing-mode: vertical-lr;
  -o-writing-mode: vertical-lr;
  -ms-writing-mode: tb-lr;
  writing-mode: vertical-lr;
  -webkit-text-orientation: sideways-right;
}
</style>



