<template>
  <div class="content mongol">
    <div class="" style="margin-top:10px;">
        <div>
          <h4 class="title">   ——                 </h4>
        </div>
        <div style="margin:0 20px;" onselectstart="return false">
          <div class="thumbnail">
              <p class="subject">
                <!-- {{qIndex+1}} .  -->
                {{exams[qIndex].examId}} . 
                <span>{{exams[qIndex].examTitle}}</span>
              </p>
              <ul style="padding-top:20px;">
                <li class="choices" v-for="(selection,index1) in exams[qIndex].selections" @click="checkEvent(index1)" :class="{active: activeIndex === index1, color1: index1 === rightIndex, color2: index1 === errorIndex}">
                  <span v-if="index1 === 0" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">A</span>
                  <span v-if="index1 === 1" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">B</span>
                  <span v-if="index1 === 2" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">C</span>
                  <span v-if="index1 === 3" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">D</span>
                  <span v-if="index1 === 4" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">E</span> {{selection}}
                </li>
              </ul>
          </div>
        </div>
    </div>

    <div style="width: 100%; height: 50px;"></div>
    <transition name="toast">
      <div class="toast" v-if="toast">
        <div class="main">{{toastText}}</div>
      </div>
    </transition>
    
    <div class="footer">
      <div :class="['btn', {btn1: collectionState}]" @click="collectionEvent"><span class="iconfont">{{collectionText}}</span></div>
      <div class="btn" @click="collectListEvent"><span class="iconfont icon-weibiaoti2fuzhi03-copy"></span><span v-if="collectNum"> {{collectNum}}</span></div>
      <div class="btn" @click="errorListEvent"><span class="iconfont icon-cuotiku"></span><span v-if="errorHtmlData.length"> {{errorHtmlData.length}}</span></div>
      <div class="btn" style="border-bottom: 1px solid #ddd;" @click="listEvent"><span class="iconfont icon-caidan-copy"></span></div>
      <div class="btn" style="margin-top:10px;" :class="[{button: qIndex !== 0}, {button1: qIndex === 0}]" @click="preEvent">ᠡᠮᠦᠨ᠎ᠡ ᠰᠡᠳᠦᠪ</div>
      <div class="btn" style="margin-top:10px;":class="[{button: qIndex !== exams.length - 1}, {button1: qIndex === exams.length - 1}]" @click="nextEvent">
          ᠳᠠᠷᠠᠭ᠎ᠠ ᠰᠡᠳᠦᠪ
        </div>
    </div>
    <u-model ref="list" type="pop" class="collectList">
      <div slot="modalbody">
        <h5>   </h5>
        <div style="overflow:auto;max-width:300px;">
          <ul class="bs-list" style="padding:5px;">
            <li v-for="item in allQuestionState">
              <div class="item" v-if="item.state === 1" @click="allQuestionEvent(item.id)">{{exams[item.id].examId}}</div>
              <div class="item right" v-if="item.state === 2" @click="allQuestionEvent(item.id)">{{exams[item.id].examId}}</div>
              <div class="item error" v-if="item.state === 3" @click="allQuestionEvent(item.id)">{{exams[item.id].examId}}</div>
            </li>
          </ul>
        </div>
      </div>
      <div slot="modelfoot"></div>
    </u-model>
    <u-model ref="collectList" type="pop" class="collectList">
      <div slot="modalbody">
        <h5></h5>
        <div style="overflow:auto;max-width:300px;">
          <ul>
            <li v-for="item in collectHtmlData">
              <div class="item right" @click="collectInEvent(item - 1)">{{exams[item-1].examId}}</div>
            </li>
          </ul>
        </div>
      </div>
      <div slot="modelfoot"></div>
    </u-model>

    <u-model ref="error" type="pop" class="collectList">
      <div slot="modalbody">
        <h5>    </h5>
        <div style="overflow:auto;max-width:300px;">
          <ul>
            <li v-for="item in errorHtmlData">
              <div class="item error" @click="errInEvent(item - 1)">{{exams[item-1].examId}}</div>
            </li>
          </ul>
        </div>
      </div>
      <div slot="modelfoot"></div>
    </u-model>

  </div>

</template>

<script>
  import data from '../../static/451TO500.json'
  import model from './Model.vue'
  export default{
    name:"exam1",
    components:{
      'u-model':model
    },
    data(){
      return{
        exams:data.list,
        // exams:[],
        questionList: [],
        activeIndex: -1,
        rightIndex: -1,
        errorIndex: -1,
        ansState: false,
        allQuestionState: [],
        errorHtmlData: [],
        collectionList:[],
        collectHtmlData: [],
        collectionState: false,
        collectionText: '',//'\&#xe62b;',
        collectNum: 0,
        toast: false,
        toastText: '',
        qIndex:0
      }
    },
    watch:{
      qIndex(n, o){
        this.qIndex = n
        this.qIndexChange()
      }
    },
    mounted(){
      for (var i = 0; i < this.exams.length; i++) {
        this.collectionList.push(false);
        this.allQuestionState.push({id: i, state: 1});// state 1:未做   2:对    3:错
      }
    },
    created(){
      // this.fnLuanXu()
      // this.fetchData()
    },
    methods:{
      checkEvent (num) {
        console.log(num)
        if (!this.questionList[this.qIndex]) { // 判断当前题目是否做过
          console.log("num")
          this.activeIndex = num;
          if (this.exams[this.qIndex].answer === num) { // 做对了
            this.rightIndex = num;
            this.questionList[this.qIndex] = {userAns: num, rightAns: num};
            this.allQuestionState[this.qIndex].state = 2;
          } else { // 做错了
            this.errorIndex = num;
            this.rightIndex = this.exams[this.qIndex].answer;
            this.questionList[this.qIndex] = {userAns: num, rightAns: this.exams[this.qIndex].answer};
            this.errorHtmlData.push(this.qIndex + 1);
            this.allQuestionState[this.qIndex].state = 3;
          }
        }
      },
      preEvent () {
        if (this.qIndex !== 0) {
          this.qIndex -= 1;
          this.activeIndex = -1;
          this.rightIndex = -1;
          this.errorIndex = -1;
          this.ansState = false;
        }
      },
      nextEvent () {
        if (this.exams.length > this.qIndex + 1) {
          this.qIndex += 1;
          this.activeIndex = -1;
          this.rightIndex = -1;
          this.errorIndex = -1;
          this.ansState = false;
        }
      },
      qIndexChange () {
        this.activeIndex = -1;
        this.rightIndex = -1;
        this.errorIndex = -1;
        if (this.questionList[this.qIndex]) { // 已答列表是否存在
          if (this.questionList[this.qIndex].rightAns === this.questionList[this.qIndex].userAns) { // 答案是否正确
            this.rightIndex = this.questionList[this.qIndex].rightAns;
          } else {
            this.rightIndex = this.questionList[this.qIndex].rightAns;
            this.errorIndex = this.questionList[this.qIndex].userAns;
          }
        }
        if (this.collectionList[this.qIndex]) { // 判断是否收藏
          this.collectionState = true;
          this.collectionText = '';
        } else {
          this.collectionState = false;
          this.collectionText = '';
        }
      },
      collectionEvent () {
        if (this.collectionState) {
          this.collectionList[this.qIndex] = false;
          this.collectionState = !this.collectionState;
          this.collectionText = '';
        } else {
          this.collectionList[this.qIndex] = true;
          this.collectionState = !this.collectionState;
          this.collectionText = '';
        }
        this.collectNum = this.getCollectList().length;
      },
      getCollectList () {
        var a = [];
        for (var i = 0; i < this.collectionList.length; i++) {
          if (this.collectionList[i]) {
            a.push(i + 1);
          }
        }
        return a;
      },
      listEvent () {
        this.$refs.list.modelOpen();
      },
      collectListEvent () {
        this.collectHtmlData = this.getCollectList();
        if (this.collectHtmlData.length > 0) this.$refs.collectList.modelOpen();
        else this.showToast('ᠬᠠᠳᠠᠭᠠᠯᠠᠭᠰᠠᠨ ᠰᠡᠳᠦᠪ ᠪᠠᠢᠬᠤ ᠦᠭᠡᠢ');
      },
      errorListEvent () {
        if (this.errorHtmlData.length > 0) this.$refs.error.modelOpen();
        else this.showToast('ᠪᠤᠷᠤᠭᠤ ᠬᠢᠭᠰᠡᠨ ᠰᠡᠳᠦᠪ ᠪᠠᠢᠬᠤ ᠦᠭᠡᠢ');
      },
      showToast (str) {
        var me = this;
        me.toast = true;
        me.toastText = str;
        setTimeout(function () {
          me.toast = false;
        }, 2000);
      },
      allQuestionEvent (num) {
        this.$refs.list.modelClose();
        this.qIndex = num;
      },
      errInEvent (num) {
        this.$refs.error.modelClose();
        this.qIndex = num;
      },
      collectInEvent (num) {
        this.$refs.collectList.modelClose();
        this.qIndex = num;
      },
      fnLuanXu() {
        this.$http.get('./../static/251TO300.json')
        .then(function(data){
          console.log(data.json());
          return data.json();
        })
          .then(function(data){
            // this.exams = data;
            var aLuanXu = data.list;
            var num = aLuanXu.length;
            console.log("num::::::::::"+num);
            for (var i = 0; i < num; i++) {
                var iRand = parseInt(num * Math.random());
                var temp = aLuanXu[i];
                aLuanXu[i] = aLuanXu[iRand];
                aLuanXu[iRand] = temp;
            }
            console.log(aLuanXu);
            this.exams = aLuanXu;
            })
      },
      fetchData(){
        this.$http.get('./../static/251TO300.json')
        .then(function(data){
          console.log(data.json());
          return data.json();
        })
        .then(function(data){
          this.exams = data.list
          //return data.list;
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .collectList {
    h5 {
      margin: 0;
      padding: 5px;
      line-height: 25px;
      text-align: center;
      color: #444;
      border-right: 1px solid var(--theme-color);
    }
    ul {
      padding: 25px;
      margin: 0 auto;
      width: 94%;
      font-size: 0;
      li {
        width: 40px;
        height: 40px;
        display: inline-block;
        position: relative;
        .item {
          width: 30px;
          height: 30px;
          border: 1px solid #aaa;
          border-radius: 50%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          font-size: 14px;
          color: #333;
          line-height: 30px;
          text-align: center;
          &:active {
            background: #ccc;
            color: #333;
          }
          &.right {
            color: #00a400;
            border: 1px solid #00a400;
            background: rgba(0, 164, 0, 0.17);
            &:active {
              background: #00a400;
              color: #fff;
            }
          }
          &.error {
            color: #e8571b;
            border: 1px solid #e8571b;
            background: rgba(255, 96, 30, 0.17);
            &:active {
              background: #e8571b;
              color: #fff;
            }
          }
        }
      }
    }
  }

  .title{
    color: var(--theme-color);
    padding-top: 0px;
    margin-left: 20px;
    font-family: urga;
    line-height: 1;
    text-align: left;
    font-size: 10px;
  }
  .subject{
    line-height: 20px;
    padding: 10px;
    margin-right: 0;
    font-family: "bichimel";
    font-size: 16px;
    color: #333;
  }
  .choices{
    list-style-type: none;
    line-height: 26px;
    cursor: pointer;
    background-color: #f9f9f9;
    margin-right: 5px;
    padding: 5px;
    font-family: "bichimel";
    font-size: 16px;
  }
  .thumbnail{
    margin:0 10px;
    border-radius: 0;
  }
  .panel{
    border: none;
  }
  .row{
    background-color: var(--background-color);
  }
  .btn-diy{
    position: relative;
    float: right;
    background-color: #333;
    color: white;
    border: 1px solid #fff;
    border-radius: 25px;
    width: 50px;
    height: 50px;
    padding-left: 10px;
    bottom: 20px;
    left: 20px;
  }
  .button {
    /* width: 80%; */
    /* line-height: 40px; */
    background: #00a400;
    /* display: block; */
    text-align: center;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
  }
  .button:active{
      background: green;

  }
  .button1 {
    cursor: pointer;
    /* width: 80%; */
    /* line-height: 40px; */
    /* display: block; */
    text-align: center;
    /* border-radius: 5px; */
    background: #ddd;
    color: #333;
  }
  .button1:active{
      background: green;
  }
  .sign {
    width: 20px;
    height: 20px;
    line-height: 18px;
    border-radius: 50%;
    display: inline-block;
    text-indent: 5.5px;
    border: 1px solid #555;
    color: #333;
    font-size: 12px;
    font-family: NotoSansHans-Regular,AvenirNext-Regular,arial,"Microsoft Yahei","Hiragino Sans GB","WenQuanYi Micro Hei",sans-serif;
  }
  .sign1 { /*成功*/
    color: #fff;
    background: #00a400;
    border: 1px solid #00a400;
  }
  .sign2 { /*失败*/
    color: #fff;
    background: #e8571b;
    border: 1px solid #e8571b;
  }
  .color1 {
    color: #00a400;
  }
  .color2 {
    color: #e8571b;
  }
  .btn-box{
    /*background-color: transparent;*/
    flex:1;
    margin-top: 10px;
    height: 320px;
    float: right;
  }
  .left{
    height: 150px;
    margin-right: 10px;
    /* display: inline; */
    float: left;
  }
  .right{
    height: 150px;
    /* display: inline; */
    float: right;
  }

  .footer {
    width: 100%;
    // line-height: 50px;
    // position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    // background: #fff;
    font-size: 14px;
    .btn {
      // margin: 10px;
      // background: #fff;
      border-radius: 0;
      width: 100%;
      text-align: center;
      border-left: 1px solid #ddd;
      border-right: 1px solid #ddd;
      border-top: 1px solid #ddd;
      // &:nth-last-child(1) {
        // border-left: none;
        // border-right:none;
      // }
      &:active {
        background: #ddd;
      }
      &.btn1 {
        color: #ffd54f;
      }
    }
  }
  .iconfont{
    font-family:"iconfont" !important;
    font-size:20px;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    // -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }

  .toast {
    width: 100%;
    position: fixed;
    top: 30%;
    left: 50%;
    min-height: 30px;
    max-height: 400px;
    opacity: 1;
    font-family: 'bichimel';
    color: #fff;
    font-size: 14px;
    &.toast-enter-active, &.toast-leave-active {
      transition: opacity 0.5s;
    }
    &.toast-enter, &.toast-leave-to {
      opacity: 0;
    }
    .main {
      background: rgba(0, 0, 0, 0.6);
      max-width: 70%;
      height: 100%;
      border-radius: 5px;
      margin: 0 auto;
      text-align: center;
      line-height: 20px;
      padding: 5px 10px;
    }
  }

</style>


